export const main = `
echo "ðŸš€ Starting setup"

# Install Homebrew if not already installed
if test ! $(which brew); then
    echo "ðŸº Installing homebrew..."
    ruby -e "$(curl -fsSL https://raw.githubusercontent.com/Homebrew/install/master/install)"
fi

echo "ðŸº Updating homebrew..."
brew update
{{PACKAGES}}{{CASKS}}{{GEMS}}
echo "ðŸ§¼ Cleaning up..."
brew cleanup -s
{{OS_SETTINGS}}
echo "ðŸŽ‰ Setup complete!"`;

export function brew(items) {
    if (items.length === 0) {
        return  "" ;
    }

    const codes = items.map(c => c.code).join("\n    ");
    return `
PACKAGES=(
    ${codes}
)
echo "ðŸº Installing brew packages..."
brew install \${PACKAGES[@]}
`
}

export function casks(items) {
    if (items.length === 0) {
        return  "" ;
    }

    const codes = items.map(c => c.code).join("\n    ");
    return `
CASKS=(
    ${codes}
)
echo "ðŸº Installing cask apps..."
brew cask install \${CASKS[@]}
`
}

export function gems(items) {
    if (items.length === 0) {
        return  "" ;
    }

    const codes = items.map(c => c.code).join("\n    ");
    return `
GEMS=(
    ${codes}
)
echo "ðŸ’Ž Installing Ruby gems..."
sudo gem install \${GEMS[@]}
`
}

export function os_settings(items) {
    if (items.length === 0) {
        return  "" ;
    }

    const codes = items.map(c => `${c.type} ${c.code}`).join("\n");
    return `
echo "ðŸ›  Configuring System..."
${codes}
echo "âš ï¸ Some changes aren't applied until you log out and back in."
`
}